}
.nav__categotiya-wrapper {
  max-width: 1280px;
  margin: 0 auto;
  /*height: 102px;
    border: 5px solid #000;*/
  /*background-color: #f8f8f8;*/
}
.nav_categoriya__chit {
  height: 32px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 85px;
  padding-right: 85px;
  background-color: #f8f8f8;
}
@media (max-width: 460px) {
  .nav_categoriya__chit {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.nav__categoriya-link {
  display: flex;
}
.nav__categoriya-link li {
  font-size: 11px;
  font-weight: 500;
  line-height: 18px;
  padding-left: 21px;
}
.nav__categoriya-link li a {
  color: #121212;
}
.nav__categoriya-link li:first-child {
  padding-left: 0;
  opacity: 0.6;
}
.nav__categoriya-link li:first-child:before {
  content: "";
}
.nav__categoriya-link li::before {
  content: "►";
  position: relative;
  left: -10px;
}

.nav__categoriya-share {
  display: flex;
}
.nav__categoriya-share p {
  font-size: 11px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  letter-spacing: 0em;
  opacity: 0.6;
}
.nav__categoriya-share img {
  width: 14px;
  height: 14px;
  opacity: 0.6;
  margin-right: 10px;
}
@media (max-width: 530px) {
  .nav__categoriya-share {
    display: none;
  }
}
.nav__categoriya-title {
  text-transform: uppercase;

  font-size: 26px;
  font-weight: 600;
  letter-spacing: 0.06em;
  text-align: center;
  background-color: #f8f8f8;
  padding-top: 10px;
  height: 70px;
}
@media (max-width: 460px) {
  .nav__categoriya-title {
    font-size: 20px;
  }
}
